type NotificationDestination = "slack" | "email"

type GenericNotificationSchedule = {
  schedulerExpression: string
  timezone: string
  destination: NotificationDestination
}

type SlackNotificationSchedule = GenericNotificationSchedule & {
  destination: "slack"
  destinationParams: {
    slackWorkspaceId: string
    slackChannelId: string
    slackChannelName: string
  }
}

type EmailNotificationSchedule = GenericNotificationSchedule & {
  destination: "email"
  destinationParams: { emails: Array<string> }
}

type NotificationSchedule = SlackNotificationSchedule | EmailNotificationSchedule

const isSlackNotificationSchedule = (
  obj: NotificationSchedule | null,
): obj is SlackNotificationSchedule => obj?.destination === "slack"

const isEmailNotificationSchedule = (
  obj: NotificationSchedule | null,
): obj is EmailNotificationSchedule => obj?.destination === "email"

type UpdateNotificationMutation = {
  queryId: string
  schedules: Array<NotificationSchedule>
  channelId?: string
  destination: NotificationDestination
  queryName: string
  deleteAll?: boolean
}

type ExistingSchedules = {
  slack: boolean
  email: boolean
} | null

export { isSlackNotificationSchedule, isEmailNotificationSchedule }
export type {
  NotificationDestination,
  NotificationSchedule,
  UpdateNotificationMutation,
  ExistingSchedules,
}
